import Vue from 'vue'
import VueRouter from 'vue-router'
import store from "./store/index";
const Store = store();

Vue.use(VueRouter)

const routes = [

    {
        path: '/',
        component: () => import('@/layouts/Layout'),
        children: [
            // Components
            {
                name: 'Login',
                path: '/login',
                component: () => import('@/views/auth/Login'),
                meta: {
                    authNotRequired: true
                }
            },

        ],
    },

    {
        path: '/views/dashboard',
        component: () => import('@/layouts/Layout'),
        children: [
            // Components
            {
                name: 'Dashboard',
                path: 'index',
                component: () => import('@/views/dashboard/BasicDashboard'),
            },

        ]
    },

    {
        path: '/views/users',
        component: () => import('@/layouts/Layout'),
        children: [
            {
                name: 'Users',
                path: 'index',
                component: () => import('@/views/pages/Users'),
            },

            {
                name: 'UserAdd',
                path: 'add',
                component: () => import('@/views/pages/UserAdd'),
            },

            {
                name: 'UserEdit',
                path: 'edit/:userId',
                component: () => import('@/views/pages/UserEdit'),
                props: true,
            },

        ]
    },

    {
        path: '/views/products',
        component: () => import('@/layouts/Layout'),
        children: [

            {
                name: 'Products',
                path: 'index',
                component: () => import('@/views/pages/Products'),
            },

            {
                name: 'ProductAdd',
                path: 'add',
                component: () => import('@/views/pages/ProductAdd'),
            },

            {
                name: 'ProductEdit',
                path: 'edit/:productId',
                component: () => import('@/views/pages/ProductEdit'),
                props: true,
            },

        ]
    },

    {
        path: '/views/orders',
        component: () => import('@/layouts/Layout'),
        children: [

            {
                name: 'Orders',
                path: 'index',
                component: () => import('@/views/pages/Orders'),
            },

            {
                name: 'OrderEdit',
                path: 'edit/:productId',
                component: () => import('@/views/pages/OrderEdit'),
                props: true,
            },

        ]
    },

    {
        path: '/views/pages',
        component: () => import('@/layouts/Layout'),
        children: [

            {
                name: 'Profile',
                path: 'profile',
                component: () => import('@/views/pages/Profile'),
            },

            
            {
                name: 'Search',
                path: 'search',
                component: () => import('@/views/pages/Search'),
            },

        ]
    },

]

const router = new VueRouter({
    mode: "hash",
    base: process.env.BASE_URL,
    routes: routes,
});

router.beforeEach(async (to, from, next) => {
    const store = await Store.getters["auth/authGetter"];
    if (
        !store.isUserLoggedIn &&
        !to.meta.authNotRequired
    )
        next({ name: "Login" });
    else next();
});


export default router